.page{
    padding-left: 3px;
    padding-right: 3px;
    height:20px;
    margin-right: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 20px;
}
.nextPrev{
   display: flex;
   cursor: pointer; 
}
.points{
    margin-right: 15px;
    display: block;
}
.active{
    background-color: black;
    color: white;
}
.container{
    display: flex;
    align-items: center;
    justify-content: center;
  li {
      list-style: none;
      
      }
      
}
.next{
    transform: rotate(270deg);
}
.prev{
    transform: rotate(90deg);
    margin-right: 15px;
}